/* 主容器 */
.guestbook-container {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  border-radius: 10px;
  background: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

/* GIF容器样式，放置在留言板上方 */
.gif-container {
  display: flex;
  justify-content: center; /* 居中显示 */
}

/* GIF 图片样式 */
.welcome-gif {
  width: 100%;  /* 设置宽度为父容器的 100% */
  max-width: 800px;  /* 限制 GIF 图片的最大宽度 */
  height: auto;  /* 保持纵横比 */
  border-radius: 8px;  /* 可选的圆角效果 */
}

/* 标题 */
h2 {
  text-align: center;
  color: #333;
}

/* 错误消息 */
.error-message {
  color: red;
  text-align: center;
  font-size: 14px;
}

/* 表单 */
.guestbook-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

/* 文本框 */
.guestbook-form textarea {
  width: 100%;
  height: 80px;
  padding: 10px;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid #ccc;
  resize: none;
}

/* 按钮和选择框 */
.form-controls {
  display: flex;
  justify-content: space-between;
}

.form-controls select,
.form-controls button {
  padding: 8px 12px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
}

.form-controls select {
  border: 1px solid #ccc;
  background: white;
}

.form-controls button {
  background: #4fa050;
  color: white;
  border: none;
}

.form-controls button:disabled {
  background: #aaa;
}

/* 留言区 */
.messages {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* 单条留言 */
.message {
  padding: 10px;
  border-radius: 10px;
  color: black; /* 留言文本颜色为黑色 */
  max-width: 80%;
  word-wrap: break-word;
}

/* 西西（you）的留言样式 */
.message.you {
  background-color: #ffcccc; /* 浅粉色 */
  align-self: flex-start;
  text-align: left;
}

/* 卜卜（me）的留言样式 */
.message.me {
  background-color: #23ce78; /* 浅蓝色 */
  align-self: flex-end;
  text-align: right;
}

/* 留言中的作者名 */
.author {
  font-weight: bold;
  margin-bottom: 5px;
}

/* 留言时间 */
.time {
  font-size: 12px;
  opacity: 0.7;
}
